@import '/_variable';

.navbar {
  padding: 15px 10px;
  background: transparent;
  border: none;
  border-radius: 0;
  color: var(--textWidgetColor);
}

.navbar-btn {
  -webkit-box-shadow: none;
          box-shadow: none;
  outline: none !important;
  border: none;
}
.navbar-brand {
  max-width: 50%;
}
.line {
  width: 100%;
  height: 1px;
  border-bottom: 1px dashed #ddd;
  margin: 40px 0;
}

#sidebarCollapse {
  width: 40px;
  height: 40px;
  background: transparent;
}

#sidebarCollapse span {
  width: 80%;
  height: 2px;
  margin: 0 auto;
  display: block;
  background: var(--textWidgetColor);
  -webkit-transition: all 0.8s cubic-bezier(0.810, -0.330, 0.345, 1.375);
  -o-transition: all 0.8s cubic-bezier(0.810, -0.330, 0.345, 1.375);
  transition: all 0.8s cubic-bezier(0.810, -0.330, 0.345, 1.375);
  -webkit-transition-delay: 0.2s;
       -o-transition-delay: 0.2s;
          transition-delay: 0.2s;
}

#sidebarCollapse span:first-of-type {
  -webkit-transform: rotate(45deg) translate(2px, 2px);
      -ms-transform: rotate(45deg) translate(2px, 2px);
          transform: rotate(45deg) translate(2px, 2px);
}

#sidebarCollapse span:nth-of-type(2) {
  opacity: 0;
}

#sidebarCollapse span:last-of-type {
  -webkit-transform: rotate(-45deg) translate(1px, -1px);
      -ms-transform: rotate(-45deg) translate(1px, -1px);
          transform: rotate(-45deg) translate(1px, -1px);
}

#sidebarCollapse.active span {
  -webkit-transform: none;
      -ms-transform: none;
          transform: none;
  opacity: 1;
  margin: 5px auto;
}

.noti {
  padding:0.5rem 0rem;
}
.noti:not(:last-child) {
  border-bottom: 1px solid #575b5f;
}
.notiList {
  overflow-y: auto;
  max-height: 30vh;
  margin-right: -10px;
  padding-right: 10px;
}
/* ---------------------------------------------------
    MEDIAQUERIES
----------------------------------------------------- */
@media (max-width: 768px) {

  .sidebar {
    margin-left: -250px;
    -webkit-transform: rotateY(90deg);
            transform: rotateY(90deg);
  }

  .sidebar.active {
    margin-left: 0;
    -webkit-transform: none;
        -ms-transform: none;
            transform: none;
  }
  .sidebar.active ~ .content{
    min-width: 100vw;
    overflow: hidden;
  }
  #sidebarCollapse span:first-of-type,
  #sidebarCollapse span:nth-of-type(2),
  #sidebarCollapse span:last-of-type {
    -webkit-transform: none;
        -ms-transform: none;
            transform: none;
    opacity: 1;
    margin: 5px auto;
  }

  #sidebarCollapse.active span {
    margin: 0 auto;
  }

  #sidebarCollapse.active span:first-of-type {
    -webkit-transform: rotate(45deg) translate(2px, 2px);
        -ms-transform: rotate(45deg) translate(2px, 2px);
            transform: rotate(45deg) translate(2px, 2px);
  }

  #sidebarCollapse.active span:nth-of-type(2) {
    opacity: 0;
  }

  #sidebarCollapse.active span:last-of-type {
    -webkit-transform: rotate(-45deg) translate(1px, -1px);
        -ms-transform: rotate(-45deg) translate(1px, -1px);
            transform: rotate(-45deg) translate(1px, -1px);
  }

}
.menu-head {
  /* max-width: 5rem; */
  display: -ms-inline-flexbox;
  display: inline-flex;
}
.menu-head i {
  font-size: 1.1rem;
}
.menu-head i.fa-save {
  color: var(--activeItem);
}
.menu-head .noti-btn {
  width: 3rem;
}


