@import '/_variable';

  .circleMenu{
    cursor: pointer;
    display:-ms-flexbox;
    display:flex; 
    -ms-flex-pack:center!important; 
        justify-content:center!important;
    border-radius:50%;
    height:60px;
    width:60px;
    background-color:var(--activeItem);
    position: fixed;
    bottom:40px;
    right:40px;
    font-size:40px;
    color:var(--textWidgetColor);
    -webkit-transition:0.3s;
    -o-transition:0.3s;
    transition:0.3s;
  }

  .circleMenu:hover{
    background-color:var(--mentionBadge);
  }
  .ModalCreate {
    background-color: var(--themeBg)
  }
  


